.wrapper{
   display: flex;
   justify-content: center;
   flex-wrap: wrap;
   gap: 20px;
   .card{
      width:30%;
      min-width: 300px;
      display: flex;
      flex-direction: column;
      padding: 15px;
      min-height: 350px;
      border: 1px solid #ccc;
      >h4{
         flex:1 1 auto;
         // min-height: 80px;
      }
      >p{
         flex: auto;
      }
      >button{
         width: 70px;
         cursor: pointer;
      }
      >.cardFooter{
         display: flex;
         align-items: center;
         justify-content: space-between;
      }
   }
}