.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;

  .wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .menu {
      list-style-type: none;
      border-right:1px solid #EDEDED ;
      > li {
        display: inline-block;
        > a {
         display: block;
         font-size: 16px;
         color: #222;
         padding: 0 6px;
        }
      }
    }
   }
}
.buttons{
  display: flex;
  align-items: center;
  button{
     border: none;
     cursor: pointer;
     padding: 5px 10px;

  }
  .login{
   margin-right: 10px;
  }
  
}
